@charset "utf-8";

*,
*:before,
*:after {
	box-sizing: border-box;
}

html,
body {
	height: 100%;
}

body {
	font-family: Century Gothic, sans-serif;
	margin: 0;
}

h1 {
	margin: 1.2em 0 .8em;
	font-size: 250%;
}

h1,
h2 {
	font-weight: normal;
}

.hidden {
	display: none !important;
}

.hidden--view {
	position: absolute !important;
	clip: rect(1px, 1px, 1px, 1px);
}

button::-moz-focus-inner {
    border: none;
}

.list-reset {
    margin: 0;
    padding: 0;
    list-style: none;
}

.list-grid > li {
    display: inline-block;
    margin: 0 .5em;
}

/* Font-size transitions when device orientation changes or browser is resized */
h1,
.instr {
    transition: font-size .2s ease-in-out;
}

.clearfix:before,
.clearfix:after {
    content: "";
    display: table;
}

.clearfix:after {
    clear: both;
}


/* ===== Components ===== */

/* View */
.view {
	overflow: hidden;
	max-width: 50em;
	margin: 0 auto;
	padding-bottom: 4em;
	text-align: center;
}

/* Action container */
.action {
	padding: .2em 0 .6em;
	border-width: 0 8px;
	border-style: solid;
	border-color: #ec1822;
	-webkit-transform: translate3d(0, 0, 0);
	transform: translate3d(0, 0, 0);
}

.action > *:last-child {
    margin-bottom: 0;
}

/* Instruction paragraph */
.instr {
	margin: 0;
	padding: 0 1em;
	font-size: 140%;
	font-style: italic;
}

/* Text field */
.field {
	margin-top: .8em;
	padding: .2em;
	border: 1px solid #555;
	border-radius: 4px;
	font-size: 150%;
	text-align: inherit;
}


/* ===== Buttons ===== */

.btn {
	padding: 0;
	background-image: none;
    background-color: #555;
    border: 1px solid #555;
    border-radius: 10px;
    color: #fff;
	font: inherit;
    cursor: pointer;
    transition: all .2s ease-in-out;
	outline: 0px dashed #666;
}

.btn:focus {
	outline-width: 1px;
}

.btn[disabled],
.btn_pressed {
	pointer-events: none;
}

.btn[disabled] {
    opacity: .4;
}

.no-hover {
	transition: all .3s ease-out 0s;
}

.btn--minor,
.btn--toggle {
    background-color: #ddd;
	border: 1px solid #bbb;
    color: #444;
}

.btn--major,
.btn_pressed {
	background-color: #ec1822;
	border-color: #ec1822;
    color: #fff;
}

.btn:not(.no-hover):hover,
.no-hover:active {
    background-color: #ddd;
    border-color: #bbb;
    color: #444;
}

.no-hover:active {
	transition-duration: 0s;
}

.btn--minor:not(.no-hover):hover,
.btn--major:not(.no-hover):hover,
.btn--toggle:not(.no-hover):hover,
.btn--minor.no-hover:active,
.btn--major.no-hover:active,
.btn--toggle.no-hover:active {
    background-color: #555;
    border-color: #555;
    color: #fff;
}


/* ===== Competitors' colours ===== */

.hong-col { color: hsl(6, 78%, 57%); }
.hong-bg { background-color: hsl(6, 78%, 57%); color: #fff; }
.hong-sbg { background-color: hsla(6, 78%, 57%, .3); }

.chong-col { color: hsl(204, 64%, 44%); }
.chong-bg { background-color: hsl(204, 64%, 44%); color: #fff; }
.chong-sbg { background-color: hsla(204, 64%, 44%, .3); }


/* ===== Shake effect for identification text fields (name, password) ===== */

.shake {
	-webkit-animation: shake 150ms 2 linear;
	animation: shake 150ms 2 linear;
}

@-webkit-keyframes shake {
	0% { -webkit-transform: translate3d(3px, 0, 0); }
	50% { -webkit-transform: translate3d(-3px, 0, 0); }
	100% { -webkit-transform: translate3d(0, 0, 0); }
}

@keyframes shake{
	0% { transform: translate3d(3px, 0, 0); }
	50% { transform: translate3d(-3px, 0, 0); }
	100% { transform: translate3d(0, 0, 0); }
}


/* ===== Error and waiting views ===== */

.err-btn,
.wa-btn {
	padding: .5em 1em;
	margin-top: 1em;
    font-size: 140%;
}


/* ===== Login view ===== */

.login-btn {
	width: 2em;
	margin-left: .2em;
	padding: .16em;
	font-size: 150%;
	border-radius: 4px;
}


/* ===== Ring List view ===== */

.rl-list {
	max-width: 18em;
	margin: 0 auto;
}

.rl-btn {
    min-width: 2.8em;
    min-height: 2.8em;
	margin-top: .7em;
    font-size: 180%;
}


/* ===== Backdrop ===== */

.backdrop {
	position: fixed;
	z-index: 50;
	top: 0;
	left: 0;
	display: table;
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, .88);
	color: #fff;
}

.bdp-body {
	display: table-cell;
	vertical-align: middle;
	text-align: center;
}

.bdp-body > p {
	margin: .5em 0;
}

.bdp-text {
	font-size: 150%;
}


/* ===== Media queries ===== */

@media (min-width: 30em) {
	
	.rl-list {
		max-width: none;
	}
	
}

@media (min-height: 22em) {
	
	h1 {
		margin: 1.4em 0 1.2em;
	}
	
}

@media (min-width: 35em) and (min-height: 26em) {
	
	h1 {
		font-size: 300%;
	}
	
	.instr {
		font-size: 150%;
	}
	
	.rl-btn {
		font-size: 230%;
	}
	
	.bdp-body {
		font-size: 130%;
	}
	
}

@media (min-width: 45em) and (min-height: 32em) {
	
	h1 {
		font-size: 400%;
	}
	
	.instr {
		font-size: 170%;
	}
	
}


@charset "utf-8";

/* ===== Ring view ===== */

#ring {
	display: table;
	width: 100%;
	max-width: none;
	height: 100%;
	padding-bottom: 0;
}

#judges-sidebar,
.main {
	display: table-cell;
	vertical-align: top;
	height: 100%;
}

.panel {
	overflow: hidden;
	height: 100%;
}


/* Match buttons */

.btns-wrap {
	max-width: 28em;
	margin: 0 auto;
}

.match-btn {
	float: left;
	display: block;
	width: 100%;
	padding: .5em .2em;
	font-size: 150%;
}

.match-btn--start,
.match-btn--end {
	width: 48%;
}

.match-btn--start {
	margin-right: 4%;
}

.match-btn--injury,
.match-btn--config,
.match-btn--endm {
	font-size: 120%;
	margin-top: .6em;
}


/* Top bar */

.rv-top {
	position: relative;
	z-index: 10;
	margin: 0 auto;
	line-height: 1.7;
	background-color: #555;
	color: #fff;
	font-size: 230%;
}


/* Rows and cells */

.rv-row {
	display: table;
	width: 100%;
	padding: 0 1em;
}

.rv-row--top {
	height: 14em;
	border-bottom: solid 1px #ccc;
}

.rv-row--bottom {
	border-top: solid 1px #ccc;
}

.rv-cell {
	display: table-cell;
	vertical-align: middle;
	width: 50%;
}

.rv-row--bottom > .rv-cell {
	vertical-align: top;
}


/* ===== Judges sidebar ===== */

#judges-sidebar {
	width: 16em;
	font-size: 90%;
	background-color: #eee;
	border-right: 1px solid #ccc;
}

.js-heading {
	margin: 0;
	line-height: 2.2;
	font-size: 200%;
	background-color: #ccc;
}

.js-btns {
	margin-top: 1.6em;
}

.js-btns > button {
	margin: 0 .1em;
	padding: .2em .5em;
	border-radius: 50%;
	font-size: 130%;
}

.js-judge {
	position: relative;
	padding: 1.6em 0;
	border-bottom: 1px solid #ccc;
}

.js-judge-name {
	margin-top: 0;
	margin-bottom: .7em;
	padding: 0 2em;
}

.js-judge-remove {
	position: absolute;
	top: 1.2em;
	right: .4em;
	display: block;
	width: 1.2em;
	line-height: 1.2em;
	padding: 0;
	border: 1px solid transparent;
	border-radius: 50%;
	background: transparent;
    color: #999;
	font-size: 130%;
	cursor: pointer;
}

.js-judge-remove:focus {
	outline: 1px dashed #666;
}

.js-judge-remove:focus,
.js-judge-remove:hover {
    background-color: #ddd;
	border: 1px solid #bbb;
    color: #444;
}

.js-judge-state {
	margin-bottom: 0;
}

.js-judge-btns {
	cursor: default; /* Prevent text cursor between the two buttons */
}

.js-judge-btn {
	padding: .3em .8em;
}


/* ===== Configuration panel ===== */

.config {
	max-width: 45em;
	margin: 3em auto;
	padding-bottom: 0;
	font-size: 110%;
	text-align: left;
}

.cf-heading {
	margin: 0 0 1em;
	text-align: center;
}

.cf-item { 
	overflow: hidden;
}

.cf-lbl,
.cf-controls {
	display: block;
	float: left;
	width: 50%;
	margin: .3em 0;
	padding: 0 .6em;
}

.cf-lbl {
	padding-top: .2em;
	padding-bottom: .2em;
	text-align: right;
}

.cf-value {
	display: inline-block;
	border: 1px solid #bbb;
	padding: .2em .5em;
	background-color: #eee;
}

.cf-btn {
	padding: .2em 0;
}

.cf-btn:not(.btn_pressed):not(:hover) {
	color: inherit;
}

.cf-btn:first-child {
	border-radius: 10px 0 0 10px;
	border-right: 0;
}

.cf-btn:last-child {
	border-radius: 0 10px 10px 0;
	border-left: 0;
}

.cf-inc,
.cf-dec {
	width: 2em;
}

.cf-true,
.cf-false {
	width: 3.5em;
}

.cf-new {
	max-width: 28em;
	margin: 3em auto;
}


/* ===== Match panel ===== */

.time-keeping,
.state-management {
	height: 50%;
}

/* Time keeping */

.tk-inner {
	position: relative;
	top: 0;
	width: 22.5em;
	margin: 0 auto;
	text-align: center;
	transition: top .3s ease-out;
}

.tk-timer {
	letter-spacing: .06em;
	color: #444;
}

.tk-timer-sep {
	position: relative;
	top: -.09em;
	text-align: center;
}

.tk-timer--round {
	font-size: 750%;
	background-color: #fff;
	transition: opacity .3s ease-in;
}

.tk-timer--injury {
	position: absolute;
	bottom: 0;
	z-index: -10;
	width: 100%;
	font-size: 300%;
	opacity: 0;
	transition: bottom .3s ease-out;
}

.tk_injury > .tk-inner {
	top: -2em;
}

.tk_injury .tk-timer--round {
	opacity: .6;
}

.tk_injury .tk-timer--injury {
	bottom: -1em;
	opacity: 1;
}

.tk_on .tk-timer--round > .tk-timer-sep,
.tk_injury .tk-timer--injury > .tk-timer-sep {/*
	animation: blink 1s .5s ease infinite reverse;*/
}


/* Scoring */

.sc-inner {
	padding: 0em 2em 2em;
	overflow: hidden;
}

.sc-judge {
	float: left;
	width: 50%;
	height: 50%;
	padding: 1em 2em;
}

.sc-judge-score > span {
	display: block;
	float: left;
	width: 50%;
	padding: .5em 0;
	font-size: 140%;
	font-weight: bold;
}


/* Penalties */

.penalties {
	padding-top: .4em;
}

.pe-heading {
	margin-bottom: .5em;
}

.pe-inner {
	margin-bottom: 1.5em;
}

.pe-inner--warnings {
	font-size: 150%;
}

.pe-item {
	display: inline-block;
	text-align: center;
	padding: 0 .5em;
}

.pe-value,
.pe-btns {
	display: inline-block;
	vertical-align: middle;
}

.pe-value {
	padding: 0 .3em;
	line-height: 1.6;
	font-size: 200%;
}

.pe-btn {
	display: block;
	width: 4rem;
	border-radius: 5px;
}

.pe-inc {
	margin-bottom: .2em;
	line-height: 2.05;
}

.pe-dec {
	line-height: 1;
	font-size: 80%;
}


/* ===== Result panel ===== */

.rp-winner {
	margin: 0;
	font-size: 450%;
	font-weight: bold;
}

.rp-winner--draw {
	color: #333;
}

.rp-results {
	margin: 2em 0 3em;
	font-size: 120%;
}

.res-table {
	margin: 0 auto;
}

.res-table th,
.res-table td {
	padding: .3em .5em;
}

.res-body th {
	text-align: right;
}

.res-row--warnings,
.res-row--fouls {
	font-size: 90%;
}

.res-row--fouls > th,
.res-row--fouls > td,
.res-row--warnings > td[rowspan="2"] {
	border-bottom: 10px solid #fff;
}

.res-row--warnings > th,
.res-row--fouls > th {
	font-weight: normal;
}

.res-head th:not(:first-child),
.res-body .hong-sbg,
.res-body .hong-bg {
	border-left: 10px solid #fff;
}

